import React from 'react'
import './experience.css';
import Aos from 'aos';
import "aos/dist/aos.css";
Aos.init();


export const experience = () => {
  const check = <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 20 20" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M6.267 3.455a3.066 3.066 0 001.745-.723 3.066 3.066 0 013.976 0 3.066 3.066 0 001.745.723 3.066 3.066 0 012.812 2.812c.051.643.304 1.254.723 1.745a3.066 3.066 0 010 3.976 3.066 3.066 0 00-.723 1.745 3.066 3.066 0 01-2.812 2.812 3.066 3.066 0 00-1.745.723 3.066 3.066 0 01-3.976 0 3.066 3.066 0 00-1.745-.723 3.066 3.066 0 01-2.812-2.812 3.066 3.066 0 00-.723-1.745 3.066 3.066 0 010-3.976 3.066 3.066 0 00.723-1.745 3.066 3.066 0 012.812-2.812zm7.44 5.252a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clip-rule="evenodd"></path></svg>
  return (
    <section id='experience' data-aos="fade-down" data-aos-duration="3000">
      <h5 id='head'>What Skill I Have</h5>
      <h2 className='h2' id='head'>
        Skills / Experience
      </h2>



      <div className='container experience_container'>
        <div className="web_dev" data-aos="flip-up" data-aos-duration="1700">
          <h3>Frontend Devlopment</h3>
          <div className="experience_content">
            <article className='experience_details'>
                <p className='experience_details-icon'>{check}</p>
              <div>              <h4>HTML</h4>
              <small className='text-light'>Experienced</small> </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>CSS</h4>
              <small className='text-light'>Intermediate</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>Javascript</h4>
              <small className='text-light'>Experienced</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>Bootstrap</h4>
              <small className='text-light'>Intermediate</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>Reactjs</h4>
              <small className='text-light'>Experienced</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>ASP.Net</h4>
              <small className='text-light'>Intermediate</small> 
              </div>
            </article>
          </div>
        </div>
        <div className="others" data-aos="flip-down" data-aos-duration="3000">
        <h3>Others</h3>
          <div className="experience_content">
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>Nodejs</h4>
              <small className='text-light'>Intermediate</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>.NET (Desktop Applications)</h4>
              <small className='text-light'>Experienced</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>Adobe XD</h4>
              <small className='text-light'>Intermediate</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>SQL Server - MongoDB - MYSQL</h4>
              <small className='text-light'>Experienced</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>Python</h4>
              <small className='text-light'>Experienced (Certified)</small> 
              </div>
            </article>
            <article className='experience_details'>
            <p className='experience_details-icon'>{check}</p>
              <div>
              <h4>Office Applications</h4>
              <small className='text-light'>Experienced</small> 
              </div>
            </article>
          </div>
        </div>
      </div>

    </section>
  )
}
export default experience
