import React from 'react'
import './services.css';
import Aos from 'aos';
import "aos/dist/aos.css";
Aos.init();


const services = () => {
  const check = <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 16 16" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M12.354 3.646a.5.5 0 010 .708l-7 7a.5.5 0 01-.708 0l-3.5-3.5a.5.5 0 11.708-.708L5 10.293l6.646-6.647a.5.5 0 01.708 0z" clip-rule="evenodd"></path><path d="M6.25 8.043l-.896-.897a.5.5 0 10-.708.708l.897.896.707-.707zm1 2.414l.896.897a.5.5 0 00.708 0l7-7a.5.5 0 00-.708-.708L8.5 10.293l-.543-.543-.707.707z"></path></svg>
  return (
    <section id='services' data-aos="zoom-in-down" data-aos-duration="3000">
      <h5 id='head'>What I Offer</h5>
      <h2 id='head'>Services</h2>

      <div className='container services_container'>
        <article className='service'>
          <div className="service_head">
            <h3>UI/UX Design</h3>
          </div>

          <ul className='service_list'>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>uiux or prototype wireframe for mobile or web </p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>Need a clean and modern interface for your business? Contact me with your ideas and i will help you bring them to life!</p>      
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>ux audit</p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>reveal critical pain points and provide real recommendations to improve your product.</p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>design Desktop Applications</p> 
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>Photo Editing</p> 
            </li>
          </ul>
        </article>



        <article className='service'>
          <div className="service_head">
            <h3>Web Development</h3>
          </div>

          <ul className='service_list'>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>Create professionnel/responsive websites </p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>Web scraping or data mining complex data structure from all types of website</p>      
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>SEO: create a professional SEO audit</p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>Business management and consulting services</p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>develop responsive Wordpress website design or blog site</p> 
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>Make a Rest API</p> 
            </li>
          </ul>
        </article>


        <article className='service'>
          <div className="service_head">
            <h3>Desktop Applications</h3>
          </div>

          <ul className='service_list'>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>develop efficient desktop application in C# sharp and vb.net</p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>design desktop application UI UX</p>      
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>convert excel spreadsheet into a desktop application</p>
            </li>
            <li>
              <big className='service_list-icon'>{check}</big>
              <p>develop modern gui based desktop applications in python</p>
            </li>
          </ul>
        </article>
      </div>
    </section>
  )
}
export default services