import React from 'react'
import './testimonial.css'
import Html from '../../assets/html.png'
import Css from '../../assets/Csss.png'
import Js from '../../assets/download-removebg-preview (2).png'
import Sass from '../../assets/sass.png'
import Node from '../../assets/download-removebg-preview (3).png'
import Reac from '../../assets/download__1_-removebg-preview.png'
import Aos from 'aos';
import "aos/dist/aos.css";
Aos.init();

export const testimonial = () => {
  return (
    <section id='testimonial' data-aos="zoom-out-down" data-aos-duration="3000">
      <h5 id='head'>Tech Stack</h5>
      <h2 id='head'>This webSite made with:</h2>

      <div className="container teckStack_container">
          <article className='techStack'>
          <div className="logo">
            <img src={Reac} alt="react_logo" />
          </div>
          <h5 className='name'>Reactjs</h5>
        </article> 
        <article className='techStack'>
          <div className="logo">
            <img src={Html} alt="html_logo" />
          </div>
          <h5>Html5</h5>
        </article>
        <article className='techStack'>
          <div className="logo">
            <img src={Css} alt="css_logo" />
          </div>
          <h5 className='name'>CSS3</h5>
        </article>
        <article className='techStack'>
          <div className="logo">
            <img src={Js} alt="Js_logo" />
          </div>
          <h5 className='name'>Javascript</h5>
        </article>
        <article className='techStack'>
          <div className="logo">
            <img src={Sass} alt="Sass" className='sass'/>
          </div>
          <h5 className='name'>Sass</h5>
        </article>
                <article className='techStack'>
          <div className="logo" >
            <img src={Node} alt="node_logo" className='node' />
          </div>
          <h5 className='name'>Noedjs</h5>
        </article>
 
      </div>
    </section>
  )
}
export default testimonial