import React from 'react'
import './portfolio.css'
import Img2 from '../../assets/attendence/student_justify (2).png'
import Img3 from '../../assets/download.png'
import Img4 from '../../assets/mfoodhub.png'
import Aos from 'aos';
import "aos/dist/aos.css";
Aos.init();



export const portfolio = () => {
  return (
    <section id='portfolio'>
      <h5>My Recent Works</h5>
      <h2>Portfolio</h2>

      <div className="container portfolio_container" data-aos="zoom-out" data-aos-duration="3000">
        <article className='portfolio_item'>
          <div className="portfolio_item_img">
            <img src={Img2} alt="" />
          </div>
          <h3>Attendence Management</h3>
          <div className='port-item-cta'>
          <a href="https://endearing-croquembouche-368239.netlify.app/"  target='_blank' className='btn btn-primary'>Live Demo</a>
          </div>
        </article>
        <article className='portfolio_item'>
          <div className="portfolio_item_img">
            <img src={Img4} alt="" />
          </div>
          <h3>MfoodHUb</h3>
          <div className='port-item-cta'>
          <a href="http://almenu.net/" target='_blank' className='btn btn-primary'>Live Demo</a>
          </div>
        </article>
        <article className='portfolio_item'>
          <div className="portfolio_item_img">
            <img src={Img3} alt="" />
          </div>

          <div className='port-item-cta'>
          <a href="#contact" target='_blank' className='btn btn-primary'>Live Demo</a>
          </div>
        </article>
      </div>
    </section>

  )
}
export default portfolio